class TokenService {
  getLocalRefreshToken() {
    const user = JSON.parse(localStorage.getItem("user"));
    return user?.refreshToken;
  }

  getLocalAccessToken() {
    const user = JSON.parse(localStorage.getItem("user"));
    return user?.token;
  }

  updateLocalAccessToken(token) {
    let user = JSON.parse(localStorage.getItem("user"));
    user.accessToken = token;
    localStorage.setItem("user", JSON.stringify(user));
  }

  getUser() {
    return JSON.parse(localStorage.getItem("user"));
  }

  getId() {
    return JSON.parse(localStorage.getItem("user")).id;
  }
  
  getRole() {
    
    return JSON.parse(localStorage.getItem("user")).role;
  }

  getStatus(){
    return JSON.parse(localStorage.getItem("user")).active;
  }

  hasRole(user, role_name) {
    let roles = user.role;
    let found = false;
    roles.forEach((role) => {
      if (role === role_name) {
        found = true;
      }
    });
    return found;
  }

  hasRoles(user, roles) {
    let hasRoles = false;
    roles.forEach((role) => {
      if (this.hasRole(user, role)) {
        hasRoles = true;
        return;
      }
    });
    return hasRoles;
  }

  //asmaa
  hasPermission(user, permission_name) {
    if (!user || !user.roles || !permission_name) return false;
    //search among all roles
    let roles = user.roles;
    let found = false;
    roles.forEach((role) => {
      if (!role.permissions) {
        return;
      }
      if (
        role.permissions.find(
          (permission) => permission.name === permission_name
        )
      ) {
        found = true;
        return;
      }
    });

    return found;
  }

  hasPermissions(user, permissions) {
    if (!user || !user.roles || !permissions) return false;
    let hasPermissions = false;
    permissions.forEach((permission) => {
      if (this.hasPermission(user, permission)) {
        hasPermissions = true;
        return;
      }
    });
    return hasPermissions;
  }



  setUser(user) { 
    localStorage.setItem("user", JSON.stringify(user));
  }

  removeUser() {
    localStorage.removeItem("user");
  }

}

export default new TokenService();